export default {
	data() {
		return {
			/* 当前标签 */
			tabCur: 0,
			/* 列表 */
			cartList: [{
				type: 1
			}, {
				type: 3
			}, {
				type: 2
			}, {
				type: 1
			}, {
				type: 2
			}, {
				type: 1
			}, {
				type: 1
			}],
			/* 模糊框 */
			modalName: null,
		}
	},
	onReachBottom() {
		uni.showLoading({
			title: '加载中',
			mask: true
		})
		setTimeout(() => {
			uni.hideLoading()
		}, 1000);
	},
	onLoad(e) {
		this.tabCur = e.type
	},
	methods: {
		/* 标签选择 */
		tabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.swiperCur = this.tabCur

		},
		/* 批量编辑 */
		edit() {
			this.isEdit = !this.isEdit
		},
		buy() {
			uni.navigateTo({
				url: '/pages/cart/order-confirm'
			})
		},
		deleteItem() {

		},
		/* 单个编辑 */
		add(item, index) {
			item.test1 += 1
		},
		subtract(item, index) {
			if (item.test1 > 1) {
				item.test1 -= 1
			}
		},
		deleteOrder(item, index) {
			let _this = this
			uni.showModal({
				title: '确认删除此订单',
				content: '删除后将无法恢复',
				success: function(res) {
					if (res.confirm) {
						_this.cartList.splice(index, 1)
						uni.showToast({
							title: '操作成功',
							icon: 'none'
						});
					} else if (res.cancel) {
						
					}
				}
			});
		},
		/* 页面跳转 */
		jump2item() {
			uni.navigateTo({
				url: '/pages/item/index'
			})
		},
		jump2shopsDetail() {
			uni.navigateTo({
				url: '/pages/shops/detail'
			})
		}
	}
}
